#main_container {
  max-width: 1400px;
  margin: 0 auto;

  @media all and (max-width:1400px) {
    padding: 0 32px;
  }
}

.swiper {
  padding-bottom: 50px;
  position: relative;
}

.swiper::after,
.swiper::before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100px;
  background: #eff3f7;
  z-index: 2;
  filter: blur(20px);
}

.swiper::after {
  left: -50px;
}

.swiper::before {
  right: -50px;
}

.swiper-slide {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 18px;

  background-color: #fff;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, .25), 0 8px 16px -8px rgba(0, 0, 0, .3), 0 -6px 16px -6px rgba(0, 0, 0, .025);

  padding: 2rem 0;
}

.slide-img {
  position: relative;

  .slide_img_gradient {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;

    display: flex;
    justify-content: flex-start;
    align-items: flex-end;

    background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.09) 20%, rgba(0, 0, 0, 0.65));

    p {
      color: white;
      font-size: 2.5rem;
      padding: 10px;
    }

    .rating_icons {
      width: 32px;
      height: 32px;
      position: absolute;
      top: 10px;
      right: 10px;
      color: white;
      background-size: cover;
    }

    .rating_all {
      background-image: url('../../../public/img/rating/all.svg');
    }

    .rating_12 {
      background-image: url('../../../public/img/rating/12.svg');
    }

    .rating_15 {
      background-image: url('../../../public/img/rating/15.svg');
    }

    .rating_18 {
      background-image: url('../../../public/img/rating/18.svg');
    }
  }
}

.slide-inner {

  margin-top: 20px;

  .movie_title {
    font-size: 1.5rem;
    font-weight: 700;
    margin-bottom: 1rem;
  }

  p:not(.movie_title) {
    margin-top: 5px;
  }
}

.slide_more {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.35);
  border-radius: 18px;
  opacity: 0;
  transition: all .35s ease-in-out;

  display: flex;
  justify-content: center;
  align-items: center;

  &:hover {
    opacity: 1;
  }

  a {
    border-radius: 10px;
    border: 1px solid #fff;

    font-size: 1.125rem;
    font-weight: 600;
    color: #fff;
    letter-spacing: -0.02em;

    padding: 0.5rem 0.875rem;

    text-decoration: none;
  }
}

@media all and (max-width:1200px) {
  .swiper-slide {
    padding: 2rem 1rem;
  }

  .slide-img {
    img {
      width: 100%;
    }
  }
}

@media all and (max-width:768px) {
  #main_container {
    padding: 0 16px;
  }

  .swiper-button-next {
    right: 0;
  }

  .swiper-button-prev {
    left: 0;
  }

  .swiper-button-prev::after,
  .swiper-button-next::after {
    font-size: 3rem;
  }

  .slide-img .slide_img_gradient .rating_icons {
    width: 4.16666vw;
    height: 4.16666vw;
    min-width: 19px;
    min-height: 19px;
    top: 1.3020833vw;
    right: 1.3020833vw;
  }
}